import React from "react";
import {BotOutput} from "./BotOutput";
import Delay from "react-delay/lib/Delay";
import {motion} from "framer-motion";

export class BotOutputs extends React.Component {
    constructor(props) {
        super(props);
        this.outputs = [{"text": "Welcome back, Gregor", "delay": 3000}, {"text": "How are you today?", "delay": 6000}];
    }


    render() {
        return (
            this.outputs.map((output) =>
                <Delay wait={output.delay}>
                    <motion.div initial={{y: 300}} animate={{y: 0}} transition={{duration: 2}}>
                        <BotOutput output={output}/>
                    </motion.div>
                </Delay>
            )
        );
    }
}
